@import url("https://use.typekit.net/qrb3wez.css");
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,400i,700,700i&display=swap');

body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    font-family: 'Open Sans', sans-serif;
}

.bold {
    font-weight: 700;
}

.nav {
    width: 100vw;
    height: 100px;
    display: inline-flex;
}

.navBlock {
    width: 25vw;
    height: 100%;
    align-items: center;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}

.navBlock:hover {
    background-color: black;
    p {
        color:white;
        transform: rotate(-15deg);
    }
}

.navBlock p {
    color: black;
    font-size: 18px;
}

.navBlock p:hover {
    
}


section {
    width: 50vw;
    height: calc(100vh - 100px);
    
}

main {
    display: inline-flex;
    width: 100vw;
}

.container {
    display: flex;
}

.sideCat {
    width: 100px;
    height: 100%;
    display: flex;
    flex-direction: column;
    font-style: italic;
}
.cat {
    width: 100%;
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cat p {
    transform: rotate(-90deg);
}

.workList {
    height: 100%;
    width: calc(50vw - 100px);
    display: grid;
}

.workItem {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: gitan-latin, sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 24px;
}

.workItem {
    text-decoration: none;
}

.workItem:hover {
    background-color: black;
    p {
        color: white;
    }
}

.workItem p {
    color: black;
}

.content {
    display: flex;
    justify-content: center;
    align-items: center;
}

.playList {
    width: 50vw;
    display: grid;
    height: calc(100vh - 100px);
}

.aboutItem {
    height: 20vh;
}

.socialList {
    height: calc(60vh - 100px);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.socialItem {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    text-align: center;
    height: 100%;
    color: black;
}

.socialItem p {
    font-family: gitan-latin, sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 24px;
    color: black;
}

.aboutContent {
    position: relative !important;
    width: 100vw;
    height: 500px !important;
}

.aboutContainer {
    display: block !important;
}

.aboutList {
    margin-top: 0px !important;
}

.contentContainer {
    padding: 5vw;
}

.contentContainer h1 {
    font-family: gitan-latin, sans-serif;
    font-weight: 500 !important;
    font-style: italic;
    font-size: 35px;
    margin-bottom: 0px;
}

.contentContainer h2 {
    margin-top: 10px;
    font-family: gitan-latin, sans-serif;
    font-weight: 500 !important;
    font-style: normal;
}

.memojiFace {
    width: 100%;
    height: 150px;
    background-image: url(../assets/img/memoji.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: 100px;
}

.memojiFace:hover {
    background-image: url(../assets/img/memoji2.png);
    cursor: url(../assets/img/cursorHeart.png), pointer;
}

.noDisplay {
    display: none !important;
}

.hoverImage {
    width: 50vw;
    height: calc(100vh - 100px);
}

.socialItem:hover {
    p {
        color: white;
    }
}

.hoverPT {
    background-color: blue !important;
}

.hoverLW {
    background-color: red !important;
}

.hoverFLUX {
    background-color: yellow !important;
}

.hoverCO {
    background-color: green !important;
}

.hoverCATB {
    background-color: blue;
}

.hoverBA19 {
    background-color: red;
}

.hoverCODE {
    background-color: yellow;
}

.hoverDRUM {
    background-color: green;
}

.hoverEND {
    background-color: cyan;
}

#ig:hover{
    background-color: #da3a91;
}

#gh:hover{
    background-color: #2c3395;
}

#li:hover{
    background-color: #2a7ae7;
    border-top: solid black 2px;
}

#sp:hover{
    background-color: #19e33a;
    border-top: solid black 2px;
}

.mobileThumb {
    width: 100vw;
    height: 50px;
}

.hoverPT, .hoverLW, .hoverFLUX, .hoverCO {
    width: 100vw;
    height: 100%;
    position: absolute;
    z-index: 10;
}